.cont {
  padding-left: 211px;
}

table {
  width: 800px;
  border-collapse: collapse;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

th,
td {
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.2);
  color: rgb(3, 3, 3);
}

th {
  text-align: left;
}

th {
  background-color: #55608f;
}

tbody {
  background: linear-gradient(45deg, #49a09d, #5f2c82);
}

tr:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

td {
  position: relative;
}

td:hover:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: -9999px;
  bottom: -9999px;
  background-color: rgba(255, 255, 255, 0.2);
  z-index: -1;
}
